import React, { useRef, useState, useEffect, useContext } from 'react';
import { useReactToPrint } from 'react-to-print';
import moment from 'moment/moment';
import { Table, Modal } from 'antd';
import userInfoContext from '../../utils/context';
import { sharedOnCell1, sharedOnCell2, sharedOnCell3, toChinese } from '../../utils/utils';
import './index.less';

const start = [
  { order: '出货清单' },
  { order: '地址: 广东省中山市南头镇', type_name: '财务：郭生，仓管：郭生，客户：郭生，司机：郭生' },
  { order: '客户: ', type_name: '日期：', model_name: 'NO: ' },
  {
    order: '序号',
    type_name: '类别',
    model_name: '型号',
    unit: '单位',
    sendCount: '数量',
    price: '单价',
    total: '金额',
    note: '备注',
    address: '地址',
  },
];

const end = [
  { order: '', unit: '总数', sendCount: '', price: '定金', hidden: true },
  { order: '大写金额', type_name: '', model_name: '', unit: '合计', sendCount: '', isTotal: true },
  { order: '制单:08', type_name: '发货方式:现金', model_name: '财务签字:', unit: '' },
];

function ExportModal({ rows, close, submit }) {
  const [data, updateData] = useState([]);
  const { currentUser = {} } = useContext(userInfoContext) || {};
  const componentRef = useRef();
  const handlePrint = useReactToPrint({
    content: () => componentRef.current,
  });

  const columns = [
    { title: '序号', dataIndex: 'order', onCell: (a, b) => sharedOnCell1(a, b, data) },
    {
      title: '类别',
      key: 'type_name',
      render: (_, record) => {
        if (record.isTotal) {
          const total = rows.reduce((prev, item) => prev + Number(item.price || 0) * Number(item.sendCount || 0), 0);
          return toChinese(total);
        }
        return record.type_name;
      },
      onCell: (a, b) => sharedOnCell2(a, b, data),
    },
    { title: '型号', dataIndex: 'model_name', width: '25%', onCell: (a, b) => sharedOnCell3(a, b, data) },
    {
      title: '单位',
      dataIndex: 'unit',
      onCell: (_, index) => ({
        colSpan: [0, 1, 2].includes(index) ? 0 : 1,
      }),
    },
    {
      title: '数量',
      dataIndex: 'sendCount',
      render: (_, record) => {
        if (record.isTotal) {
          return rows.reduce((prev, item) => prev + Number(item.price || 0) * Number(item.sendCount || 0), 0);
        }
        return record.sendCount;
      },
      onCell: (_, index) => ({
        colSpan: [0, 1, 2].includes(index) ? 0 : 1,
      }),
    },
    {
      title: '单价',
      dataIndex: 'price',
      onCell: (_, index) => ({
        colSpan: [0, 1, 2, data.length - 1].includes(index) ? 0 : 1,
      }),
    },
    {
      title: '金额',
      key: 'total',
      render: (_, record, index) => {
        if (index === 3) {
          return record.total;
        }
        if (record.hidden) {
          return null;
        }
        if (record.sendCount) {
          return Number(record.sendCount) * Number(record.price);
        }

        return null;
      },
      onCell: (_, index) => ({
        colSpan: [0, 1, 2, data.length - 1].includes(index) ? 0 : 1,
      }),
    },
    {
      title: '备注',
      dataIndex: 'note',
      onCell: (_, index) => ({
        colSpan: [0, 1, 2, data.length - 1, data.length - 2].includes(index) ? 0 : 1,
      }),
    },
    {
      title: '地址',
      dataIndex: 'address',
      onCell: (_, index) => ({
        colSpan: [0, 1, 2, data.length - 1, data.length - 2].includes(index) ? 0 : 1,
      }),
    },
  ];

  useEffect(() => {
    const row = rows.map((o, index) => ({ ...o, order: index + 1, unit: '台' }));
    if (row.length < 14) {
      for (let i = row.length + 1; i < 14; i++) {
        row.push({ order: i });
      }
    }
    start[2] = {
      order: `客户: ${rows[0].custom_name}`,
      type_name: `日期: ${moment(new Date()).format('YYYY-MM-DD HH:mm:ss')}`,
      model_name: `NO: ${moment(new Date()).format('YYYYMMDDHHmmss')}`,
    };
    end[0].order = row.length + 1;
    end[0].sendCount = rows.reduce((prev, item) => prev + Number(item.sendCount || 0), 0);
    end[2].unit = `业务员: ${currentUser.name}`;
    const newData = start.concat(row).concat(end);
    updateData(newData);
  }, []);

  function onOk() {
    submit();
    close(false);
    handlePrint();
  }

  return (
    <Modal title="打印" open cancelText="取消" okText="打印" onCancel={() => close(false)} onOk={onOk} width="1000px" bodyStyle={{ padding: 0 }}>
      <div ref={componentRef} className="exportContent">
        <Table columns={columns} size="small" dataSource={data} bordered pagination={false} />
      </div>
    </Modal>
  );
}

export default ExportModal;
