import React, { Fragment, useState, useEffect } from 'react';
import moment from 'moment/moment';
import { Card, Input, InputNumber, Table, DatePicker, Button, Form, message } from 'antd';
import { SearchOutlined, DownloadOutlined, TableOutlined } from '@ant-design/icons';
import useLoading from '../hooks/useLoading';
import ExportModal from './ExportModal';
import { getOrderList, updateOrderSentCount } from '../../api/api';
import { createDownLoadData, formatExportData } from '../../utils/utils';
import 'moment/locale/zh-cn';
import locale from 'antd/es/date-picker/locale/zh_CN';

function SellPrint() {
  const [visible, updateVisible] = useState(false);
  const [data, updateData] = useState([]);
  const [pagination, updatePagination] = useState({ current: 1, pageSize: 30 });
  const [rows, updateRows] = useState([]);
  const [loading, req] = useLoading(getOrderList);

  const columns = [
    { title: '单号', width: 180, dataIndex: 'order_num', key: 'id', fixed: 'left' },
    {
      title: '日期',
      width: 180,
      key: 'date',
      render: (_, record) => moment(record.create_time).format('YYYY-MM-DD HH:mm:ss'),
      fixed: 'left',
    },
    { title: '客户', width: 80, dataIndex: 'custom_name', fixed: 'left' },
    { title: '业务员', width: 80, dataIndex: 'create_by_nickname', fixed: 'left' },
    { title: '完成状态', dataIndex: 'status' },
    { title: '类别', dataIndex: 'type_name' },
    { title: '型号', dataIndex: 'model_name' },
    { title: '品牌', dataIndex: 'brand_name' },
    { title: '备注', dataIndex: 'note' },
    { title: '数量', dataIndex: 'count' },
    {
      title: '发货数',
      dataIndex: 'sendCount',
      render: (_, record) => <InputNumber style={{ width: '90%' }} size="small" onChange={(e) => changeSendCount(e, record)} />,
    },
    { title: '已发量', dataIndex: 'sent_count' },
    { title: '单价', dataIndex: 'price' },
    {
      title: '总价',
      dataIndex: 'total_price',
      render: (_, record) => {
        return record.total_price ? record.total_price : Number(record.count) * Number(record.price);
      },
    },
    { title: '定金', dataIndex: 'deposit' },
    { title: '完成状态', dataIndex: 'status' },
    { title: '存放地址', dataIndex: 'storage_address' },
    { title: '发货方式', dataIndex: 'pay_type' },
  ];

  useEffect(() => {
    getOrder();
  }, []);

  function changeSendCount(value, item) {
    const current = data.find((d) => d.id === item.id);
    current.sendCount = value;
    const currentRow = rows.find((o) => o.id === item.id);
    if (currentRow) {
      currentRow.sendCount = value;
      updateRows([...rows]);
    }
    updateData([...data]);
  }

  function getOrder(values) {
    req(values).then((res) => {
      if (res?.code === 200) {
        const { list, pagination } = res?.result;
        updateData(list);
        updatePagination(pagination);
      }
    });
  }

  function onChange(selectedRowKeys, selectedRows) {
    updateRows(selectedRows);
  }

  function pageChange(p, size) {
    getOrder({ current: p, pageSize: size });
  }

  function preview() {
    if (!rows.length) {
      return message.error('请先选择要打印的数据');
    }
    if (rows.some((o) => !o.sendCount)) {
      return message.error('发货数不能为空');
    }
    if (rows.some((o) => o.status !== '入库完成')) {
      return message.error('只有入库完成状态的才能打印');
    }
    updateVisible(true);
  }

  function onSearch(values) {
    const { customName } = values;
    const time = values?.time || {};
    const startTime = time[0] instanceof moment ? moment(time[0]).format('YYYY-MM-DD HH:mm:ss') : '';
    const endTime = time[1] instanceof moment ? moment(time[1]).format('YYYY-MM-DD HH:mm:ss') : '';
    getOrder({ startTime, endTime, customName });
  }

  function download() {
    if (!rows.length) {
      return message.error('请先选择要导出的数据');
    }

    let list = JSON.parse(JSON.stringify(rows));
    list = list.map((item) => ({
      订单编号: item.order_num,
      日期: moment(item.create_time).format('YYYY-MM-DD HH:mm:ss'),
      客户: item.custom_name,
      业务员: item.create_by_nickname,
      类别: item.type_name,
      型号: item.model_name,
      品牌: item.brand_name,
      数量: item.count,
      实到量: item.real_count,
      已发量: item.sent_count,
      单价: item.price,
      总价: item.total_price ? item.total_price : Number(item.count) * Number(item.price),
      备注: item.note,
      存放地址: item.storage_address,
    }));
    createDownLoadData(list,true);
  }

  async function submit() {
    const payload = rows.map((item) => ({
      id: item.id,
      sendCount: item.sendCount,
    }));
    const res = await updateOrderSentCount({ payload });
    if (res.code === 200) {
      getOrder();
    }
  }

  return (
    <Fragment>
      <Card bordered={false}>
        <div style={{ marginBottom: 10, display: 'flex' }}>
          <Form onFinish={onSearch} layout="inline">
            <Form.Item name="time">
              <DatePicker.RangePicker showTime placeholder={['开始时间', '结束时间']} locale={locale} />
            </Form.Item>

            <Form.Item name="customName">
              <Input placeholder="输入客户名" style={{ width: 150, margin: '0 10px' }} />
            </Form.Item>

            <Form.Item>
              <Button type="primary" icon={<SearchOutlined />} htmlType="submit">
                搜索
              </Button>
            </Form.Item>
          </Form>
          <Button type="primary" icon={<TableOutlined />} onClick={preview} style={{ marginRight: 10 }}>
            预览打印
          </Button>
          <Button type="primary" icon={<DownloadOutlined />} onClick={download}>
            导出
          </Button>
        </div>

        <Table
          rowKey="id"
          loading={loading}
          dataSource={data}
          columns={columns}
          rowSelection={{ onChange }}
          pagination={{ pageSize: 30 }}
          bordered
          scroll={{ x: 1600 }}
          size="small"
        />
      </Card>

      {visible && <ExportModal rows={rows} close={updateVisible} submit={submit} />}
    </Fragment>
  );
}

export default SellPrint;
